.Intro {
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
  color: white;
}

.Intro-content {
  display: flex;
  height: 100%;
  font-size:large;
  flex-direction: column;
}

.Intro-title {
  font-size: 800%;
  margin: 0;
}

.Insta-link {
  font-size: 300%;
  color: black;
  text-decoration: none;
  margin-top: 100px;
  margin-bottom: 150px;
}

.IntroSub {
    font-size: 250%;
    margin: 100px, 100px, 100px, 100px;
}

.Intro-button {
	  margin: 10px;
}


.Resume-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.Resume {
  height: auto;
  background-color: rgb(240, 240, 240);
}

.Resume-title{
  font-size: 300%;
}

.Resume-text {
  font-size: 150%;
}

.Resume-image {
    margin: 150px 100px 150px 100px;
    width: 25%;
    height: 48%;
    color: black;
}

.Resume-link {
  color: rgb(114,114, 114);;
  text-decoration: none;
}


.Showroom {
   color: white;
   background-color: rgb(25, 25, 25);
}

.Showroom-content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.Showroom-images {
  display: flex;
  flex-direction: row;
  margin-top: 50px;
  margin-bottom: 150px;
  align-items: center;
  justify-content: center;
}

.Showroom-image-box {
  width: 25%;
  height: 31%;
  margin: 20px 50px 20px 50px;
  display: flex;
}

.Showroom-image {
  alight-item: center;
  width: 100%;
  height: 100%;
}

.Showroom-image-middle {
  padding-top: 100px;
}

.Showroom-title {
  margin-top: 150px;
  margin-bottom: 50px;
  font-size: 450%;
}

.Showroom-text {
  font-size: 150%;
  max-width: 60%;
  text-align: center;
}

.Wato {
    height: auto;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
}

.Wato-content {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 120%;
}

.Wato-title, .Wato-text {
  text-align: center;
  color: black;
}

.Wato-title {
  margin-bottom: 50px;
  margin-top: 150px;
  font-size: 350%;
  max-width: 50%;
}

.Wato-text {
  font-size: 120%;
  margin-bottom: 50px;
  max-width: 60%;
}

.Wato-button {
  margin-top: 50px;
  margin-bottom: 150px;
}

.AdBreak {
    height: auto;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
}

.Ad-content {
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.Ad-text {
  font-size: 350%;
  max-width: 70%;
  text-align: center;
  color: white;
  margin-top: 150px;
  margin-bottom: 50px;
}

.Ad-button {
  margin-bottom: 150px;
}

.Yo-content {
  display: flex;
  height: auto;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.Yo-images {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.Yo-image-box {
  width: 25%;
  height: 25%;
  margin: 10px 10px 10px 10px;
  display: flex;
}

.Yo-image {
  alight-item: center;
  width: 100%;
  height: 100%;
  margin: 20px 20px 20px 20px;
}

.Yo-title {
  margin-top: 150px;
  margin-bottom: 100px;
  font-size: 450%;
}

.Yoyo {
}

.Footer {
    height: auto;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
    background-color: black;
    magin-bottom: 150px;
}

.Footer-content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.Footer-text {
  font-size: 350%;
  max-width: 70%;
  text-align: center;
  color: white;
  margin-top: 100px;
}

.Footer-nav {
  text-align: center;
  margin-top: 50px;
  margin-bottom: 50px;
  color: white;
}

.Footer-link {
  font-size: 90%;
  text-decoration: none;
  margin-top: 100px;
  margin-bottom: 150px;
  color: white;
}


.Contact {
  text-align: center;
  margin-bottom: 100px;
}

.Contact-link {
  font-size: 150%;
  color: white;
  text-decoration: none;
  margin-top: 100px;
  margin-bottom: 150px;
}

@media (prefers-reduced-motion: no-preference) {
  .App {
    background-position: center center;
  }

  .Intro-content {
    align-items: left;
    justify-content: center;
    margin-left: 250px;
  }
}
  
